___CSS_LOADER_EXPORT___.push([module.id, ".by-preview[data-v-0c5d89a4]{height:100%;min-width:1070px;width:100%}iframe[data-v-0c5d89a4]{border:none;height:100%!important;scale:(.7);width:100%!important}.padre[data-v-0c5d89a4]{overflow-x:visible;white-space:nowrap}.hijo[data-v-0c5d89a4]{transform:scale(.6)}@media (max-width:600px){.botones[data-v-0c5d89a4]{justify-content:space-between;margin-bottom:5px;margin-left:-5px;margin-right:-10px}.botones[data-v-0c5d89a4],.btn[data-v-0c5d89a4]{width:100%}.inputs[data-v-0c5d89a4]{display:block}}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Admin/Reportes/index.vue"],"names":[],"mappings":"AAkYA,6BAEA,WAAA,CACA,gBAAA,CAFA,UAGA,CACA,wBACA,WAAA,CAEA,qBAAA,CACA,UAAA,CAFA,oBAGA,CACA,wBACA,kBAAA,CACA,kBACA,CACA,uBACA,mBACA,CACA,yBACA,0BAEA,6BAAA,CAGA,iBAAA,CAFA,gBAAA,CACA,kBAEA,CACA,gDANA,UAQA,CACA,yBACA,aACA,CAEA","sourcesContent":["<template>\r\n<v-container style=\"background:white\">\r\n  <div class=\"\" style=\"background:white;\">\r\n    <div class=\"text-center\">\r\n      <v-snackbar\r\n        v-model=\"snackbar\"\r\n        :timeout=\"timeout\"\r\n      >\r\n        {{ text }}\r\n\r\n        <template v-slot:action=\"{ attrs }\">\r\n          <v-btn\r\n            color=\"blue\"\r\n            text\r\n            v-bind=\"attrs\"\r\n            @click=\"snackbar = false\"\r\n          >\r\n            Close\r\n          </v-btn>\r\n        </template>\r\n      </v-snackbar>\r\n    </div>\r\n\r\n    <v-row class=\"inputs\" style=\"background: white\" >\r\n        <v-col sx=\"12\" sm=\"12\" md=\"4\" lg=\"4\" style=\"margin-bottom:-40px;\" class=\" \" >\r\n            <v-autocomplete\r\n                v-model=\"ofiE\"\r\n                clearable\r\n                dense\r\n                label=\"Oficina\"\r\n                outlined\r\n                :items=\"oficinas\"\r\n                :filter=\"customFilter\"\r\n                item-value=\"id\"\r\n                item-text=\"nombre\"\r\n                :search-input.sync=\"oficinas_search\"\r\n                required\r\n            >\r\n                <template v-slot:no-data>\r\n                    <v-list-item>\r\n                        <v-list-item-title>\r\n                            <template v-if=\"oficinas_search?.length > 0\">\r\n                                Datos no encontrados para\r\n                                <!-- <strong>\r\n                                    {{ oficinas_search }}\r\n                                </strong> -->\r\n                            </template>\r\n                            <template v-else>\r\n                                Digite más de\r\n                                <strong> 2</strong> caracteres.\r\n                            </template>\r\n                        </v-list-item-title>\r\n                    </v-list-item>\r\n                </template>\r\n\r\n                <template v-slot:item=\"data\">\r\n                    <v-list-item-content>\r\n                        <v-list-item-title v-html=\"data.item.codigo\">\r\n                        </v-list-item-title>\r\n                        <v-list-item-subtitle>\r\n                            {{ data.item.nombre }}\r\n                        </v-list-item-subtitle>\r\n                    </v-list-item-content>\r\n                </template>\r\n            </v-autocomplete>\r\n\r\n        </v-col>\r\n        <v-col sx=\"12\" sm=\"12\" md=\"4\" lg=\"4\" style=\"margin-bottom:-40px;\" class=\"p-0 \" >\r\n            <v-autocomplete\r\n                v-model=\"areE\"\r\n                clearable\r\n                class=\"mt-0 pt-0\"\r\n                dense\r\n                label=\"Area\"\r\n                outlined\r\n                :items=\"areas\"\r\n                :filter=\"customFilter\"\r\n                item-value=\"id\"\r\n                item-text=\"nombre\"\r\n                :search-input.sync=\"areas_search\"\r\n                required\r\n            >\r\n                <template v-slot:no-data>\r\n                    <v-list-item>\r\n                        <v-list-item-title>\r\n                            <template v-if=\"oficinas_search?.length > 0\">\r\n                                Datos no encontrados para\r\n                                <strong>\r\n                                    {{ oficinas_search }}\r\n                                </strong>\r\n                            </template>\r\n                            <template v-else>\r\n                                Digite más de\r\n                                <strong> 2</strong> caracteres.\r\n                            </template>\r\n                        </v-list-item-title>\r\n                    </v-list-item>\r\n                </template>\r\n\r\n                <template v-slot:item=\"data\">\r\n                    <v-list-item-content>\r\n                        <v-list-item-title v-html=\"data.item.codigo\">\r\n                        </v-list-item-title>\r\n                        <v-list-item-subtitle>\r\n                            {{ data.item.nombre }}\r\n                        </v-list-item-subtitle>\r\n                    </v-list-item-content>\r\n                </template>\r\n            </v-autocomplete>\r\n\r\n        </v-col>\r\n\r\n        <v-col sx=\"12\" sm=\"12\" md=\"4\" lg=\"4\" style=\"margin-bottom:-40px;\" class=\"p-0\" >\r\n            <v-autocomplete\r\n                v-model=\"perE\"\r\n                clearable\r\n                class=\"mt-0 pt-0\"\r\n                dense\r\n                label=\"Persona\"\r\n                outlined\r\n                :items=\"personas\"\r\n                :filter=\"customFilterPersona\"\r\n                item-value=\"id\"\r\n                item-text=\"nombres\"\r\n                :search-input.sync=\"personas_search\"\r\n                required\r\n            >\r\n                <template v-slot:no-data>\r\n                    <v-list-item>\r\n                        <v-list-item-title>\r\n                            <template v-if=\"oficinas_search?.length > 0\">\r\n                                Datos no encontrados para\r\n                                <strong>\r\n                                    {{ oficinas_search }}\r\n                                </strong>\r\n                            </template>\r\n                            <template v-else>\r\n                                Digite más de\r\n                                <strong> 2</strong> caracteres.\r\n                            </template>\r\n                        </v-list-item-title>\r\n                    </v-list-item>\r\n                </template>\r\n\r\n                <template v-slot:item=\"data\">\r\n                    <v-list-item-content>\r\n                        <v-list-item-title v-html=\"data.item.dni\">\r\n                        </v-list-item-title>\r\n                        <v-list-item-subtitle>\r\n                            {{ data.item.nombres }} {{ data.item.paterno }} {{ data.item.materno }}\r\n                        </v-list-item-subtitle>\r\n                    </v-list-item-content>\r\n                </template>\r\n            </v-autocomplete>\r\n\r\n        </v-col>\r\n    </v-row>\r\n    <div style=\"overflow-x:scroll; overflow-y:hidden; width:100%; margin-top:30px; height:460px;\">\r\n        <div  v-if=\"areE !== null && perE !== null\"  class=\"by-preview\" style=\"transform:scale(1);\">\r\n            <iframe :src=\"preview\" scrolling=\"yes\" frameborder=\"0\" style=\" padding-top:-30px;\"> </iframe>\r\n        </div>\r\n    </div>\r\n    <v-col style=\"display: flex; justify-content:flex-end;\" sx=\"12\" sm=\"12\" md=\"12\" lg=\"12\" >\r\n        <div class=\"ml-2\">\r\n            <v-btn height=\"38\" small class=\"btn\" dark color=\"primary\" @click=\"dialog = true\">Guardar PDF</v-btn>\r\n        </div>\r\n        <!-- <div class=\" ml-2\" small>\r\n            <v-btn height=\"38\" class=\"btn\" dark color=\"primary\" @click=\"Guardar\" >Guardar PDF</v-btn>\r\n        </div> -->\r\n    </v-col>\r\n\r\n    <v-row justify=\"center\">\r\n        <v-dialog\r\n        v-model=\"dialog\"\r\n        persistent\r\n        max-width=\"290\"\r\n            >\r\n            <v-card>\r\n            <v-card-title class=\"text-h5\">\r\n            Precaución..!!\r\n            </v-card-title>\r\n            <v-card-text>Se bloquearan todos los bienes de este documento</v-card-text>\r\n            <v-card-actions>\r\n            <v-spacer></v-spacer>\r\n            <v-btn\r\n                color=\"green darken-1\"\r\n                text\r\n                @click=\"dialog = false\"\r\n            >\r\n                Cancelar\r\n            </v-btn>\r\n            <v-btn\r\n                color=\"green darken-1\"\r\n                text\r\n                @click=\"generarPDF\"\r\n            >\r\n                Continuar\r\n            </v-btn>\r\n            </v-card-actions>\r\n        </v-card>\r\n        </v-dialog>\r\n    </v-row>\r\n</div>\r\n\r\n\r\n  </v-container>\r\n</template>\r\n<script>\r\nimport Layout from \"@/Layouts/AdminLayout\";\r\nimport axios from 'axios';\r\nexport default {\r\n    metaInfo: { title: \"Personas\" },\r\n    layout: Layout,\r\n    data () {\r\n      return {\r\n        url:'',\r\n        dialog: false,\r\n        preview:'',\r\n        searchdocuments:\"\",\r\n        areas: [],\r\n        oficinas: [],\r\n        personas:[],\r\n        documentos:[],\r\n        areEO:null,\r\n        areE:null,\r\n        perE:null,\r\n        ofiE:null,\r\n        snackbar: false,\r\n        text: '',\r\n        PDF:null,\r\n        timeout: 2000,\r\n        oficinas_search: \"\",\r\n        areas_search: \"\",\r\n        personas_search: \"\",\r\n        data: [],\r\n      }\r\n    },\r\n    created() {\r\n        this.getAreas()\r\n        this.getPersonas()\r\n        this.getOficinas()\r\n    },\r\n\r\n    watch:{\r\n        areE: function(){\r\n            this.getPersonas();\r\n            this.buscabyOficinaID(this.areEO)\r\n            this.preview =  '/admin/reportes/preview/'+this.areE+'/'+this.perE+'#toolbar=0';\r\n        },\r\n        perE: function(){\r\n            this.getAreas();\r\n            this.preview =  '/admin/reportes/preview/'+this.areE+'/'+this.perE+'#toolbar=0';\r\n        },\r\n        ofiE: function(){\r\n            this.getAreas();\r\n        }\r\n    },\r\n\r\n    methods: {\r\n        customFilter(item, queryText, itemText) {\r\n            const nombre = item.nombre.toLowerCase();\r\n            const codigo = item.codigo.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                nombre.indexOf(searchText) > -1 ||\r\n                codigo.indexOf(searchText) > -1\r\n             );\r\n        },\r\n        customFilterPersona(item, queryText, itemText) {\r\n            const nombres = item.nombres.toLowerCase();\r\n            const dni = item.dni.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                nombres.indexOf(searchText) > -1 ||\r\n                dni.indexOf(searchText) > -1\r\n             );\r\n        },\r\n\r\n        async getOficinas() {\r\n            if (this.areE === null ){\r\n                let res = await axios.get(\"/admin/oficinas/getallOficinas\");\r\n                console.log(res.data);\r\n                this.oficinas = res.data.datos;\r\n                return res.data.datos.data;\r\n            } else {\r\n                if(this.areE !== null ){\r\n\r\n                }\r\n            }\r\n        },\r\n\r\n        async getAreas() {\r\n            if (this.perE === null && this.ofiE === null){\r\n                let res = await axios.get(\"/admin/areas/getAreasAllInv\");\r\n                this.areas = res.data.datos;\r\n                this.areas2 = res.data.datos;\r\n                return res.data.datos.data;\r\n            }\r\n            else\r\n            {\r\n                if (this.ofiE !== null  && this.perE === null){\r\n                    let res = await axios.get(\"/admin/areas/getAreasByOficinaInv/\"+this.ofiE);\r\n                    this.areas = res.data.datos;\r\n                    return res.data.datos.data;\r\n                }else {\r\n                    if (this.ofiE === null && this.perE !== null ){\r\n                        let res = await axios.get(\"/admin/areas/getAreasByPersonaInv/\"+this.perE);\r\n                        this.areas = res.data.datos;\r\n                        return res.data.datos.data;\r\n                    }\r\n\r\n                }\r\n\r\n            }\r\n\r\n        },\r\n        async getPersonas() {\r\n            if (this.areE === null ){\r\n                let res = await axios.get(\"/admin/personas/getPersonasInv\");\r\n                console.log(res.data);\r\n                this.personas = res.data.datos;\r\n                return res.data.datos.data;\r\n            }\r\n            else {\r\n                let res = await axios.get(\"/admin/personas/getPersonasByAreaInv/\"+this.areE);\r\n                console.log(res.data);\r\n                this.personas = res.data.datos;\r\n                return res.data.datos.data;\r\n            }\r\n        },\r\n        buscabyID(id){\r\n            for(let i in this.personas){\r\n                if (this.personas[i].id === id ){\r\n                    return this.personas[i].dni\r\n                }\r\n\r\n            }\r\n        },\r\n        buscabyOficinaID(id){\r\n            console.log(id)\r\n            for(let i in this.oficinas){\r\n                if (this.oficinas[i] === id ){\r\n                    this.ofiE = this.oficinas[i].id\r\n                    this.oficinaElegida = this.oficinas[i].nombre\r\n                }\r\n            }\r\n        },\r\n        buscaAreabyID(id){\r\n            for(let i in this.areas2){\r\n                if (this.areas2[i].id === id ){\r\n                    return this.areas2[i].nombre\r\n                }\r\n\r\n            }\r\n        },\r\n        generarPDF(){\r\n            this.dialog = false;\r\n            let res = axios.get(\"/admin/pdfBienes/\"+this.perE+\"/\"+this.areE)\r\n            .then(response => {\r\n                 console.log(response);\r\n                 this.PDF=response.data.datos;\r\n//                 this.url = this.PDF.url+\"#toolbar=0\";\r\n                 this.url = this.PDF.url;\r\n             });\r\n\r\n            this.text = \"Documento Guardado\"\r\n            this.snackbar = true\r\n        },\r\n\r\n        async Guardar() {\r\n            this.PDF['id_persona'] = this.perE;\r\n            let res = await axios.post(\r\n                \"/admin/documentos/guardar\",\r\n                this.PDF\r\n            );\r\n            console.log(res.data);\r\n            if (this.is_nuevo) {\r\n                this.$refs.form_user.reset();\r\n            }\r\n\r\n        },\r\n\r\n    },\r\n  }\r\n</script>\r\n<style scoped>\r\n.by-preview{\r\n    width:100%;\r\n    height:100%;\r\n    min-width:1070px;\r\n}\r\niframe{\r\nborder:none;\r\nwidth: 100% !important;\r\n  height: 100% !important;\r\n  scale:(0.7);\r\n}\r\n.padre{\r\n   overflow-x: visible;\r\n   white-space: nowrap;\r\n  }\r\n.hijo{\r\n    transform: scale(0.6)\r\n}\r\n@media (max-width: 600px) {\r\n  .botones {\r\n    width: 100%;\r\n    justify-content: space-between;\r\n    margin-left: -5px;\r\n    margin-right: -10px;\r\n    margin-bottom: 5px;\r\n  }\r\n  .btn{\r\n    width: 100%;\r\n  }\r\n    .inputs{\r\n        display: block;\r\n    }\r\n\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
